.user {
	display: flex;
	margin-bottom: 20px;
}

.user:last-child {
	margin-bottom: 0;
}

.user__left {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.user__avatar-container {
	position: relative;
	width: 140px;
	height: 140px;
	border-radius: 50%;
	overflow: hidden;
}

.user__avatar {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	object-fit: cover;
}

.user__btn {
	box-shadow: inset 0 1px 1px rgba(111, 55, 125, 0.8), inset 0 -1px 0px rgba(63, 59, 113, 0.2), 0 9px 16px 0 rgba(0, 0, 0, 0.3), 0 4px 3px 0 rgba(0, 0, 0, 0.3), 0 0 0 1px #150a1e;
	background-image: linear-gradient(#3b2751, #271739);
	text-shadow: 0 0 21px rgba(223, 206, 228, 0.5), 0 -1px 0 #311d47;
	color: white;
}

.user__btn_disabled {
	background-image: none;
	background-color: grey;
	box-shadow: inset 0 1px 1px rgba(111, 55, 125, 0.8), inset 0 -1px 0px rgba(63, 59, 113, 0.2), 0 9px 16px 0 rgba(0, 0, 0, 0.3), 0 4px 3px 0 rgba(0, 0, 0, 0.3), 0 0 0 1px grey;
}

.user__data {
	border: 1px solid grey;
	border-radius: 10px;
	flex: 1 1 auto;
	display: flex;
	justify-content: space-between;
	max-width: 600px;
	margin-bottom: 10px;
	margin-left: 10px;
	padding: 15px;
	
}